import React, { useState } from "react";
import "./QuizPage.css";
import jsPDF from "jspdf"; // استيراد مكتبة jsPDF لإنشاء الشهادات PDF
import certificateBg from "../../images/cert.png"; // تأكد من المسار

const questions = [
	{
		question: "What does HTML stand for?",
		options: [
			"Hyper Trainer Marking Language",
			"Hyper Text Markup Language",
			"Hyper Text Markdown Language",
			"High Text Markup Language",
		],
		answer: 1,
	},
	{
		question: "Which tag is used to create a paragraph in HTML?",
		options: ["<para>", "<p>", "<pg>", "<text>"],
		answer: 1,
	},
	{
		question: "Which tag is used to create the largest heading?",
		options: ["<h6>", "<heading>", "<h1>", "<head>"],
		answer: 2,
	},
	{
		question: "Which attribute specifies a link’s destination?",
		options: ["src", "href", "link", "target"],
		answer: 1,
	},
	{
		question: "Which tag is used to insert an image?",
		options: ["<img>", "<image>", "<src>", "<pic>"],
		answer: 0,
	},
	{
		question: "HTML elements are represented by?",
		options: ["Tags", "Functions", "Variables", "Objects"],
		answer: 0,
	},
	{
		question: "Which tag is used for an unordered list?",
		options: ["<ol>", "<ul>", "<list>", "<li>"],
		answer: 1,
	},
	{
		question: "What is the purpose of the alt attribute in <img>?",
		options: [
			"To create a caption",
			"To store image link",
			"To define image height",
			"To show alternative text if image fails",
		],
		answer: 3,
	},
	{
		question: "Which tag is used to create a hyperlink?",
		options: ["<link>", "<a>", "<href>", "<nav>"],
		answer: 1,
	},
	{
		question: "Which HTML tag defines emphasized text?",
		options: ["<b>", "<i>", "<em>", "<strong>"],
		answer: 2,
	},
	{
		question: "HTML files must end with which extension?",
		options: [".ht", ".html", ".web", ".txt"],
		answer: 1,
	},
	{
		question: "Which of the following is a semantic HTML tag?",
		options: ["<div>", "<span>", "<section>", "<b>"],
		answer: 2,
	},
	{
		question: "What does the <br> tag do?",
		options: [
			"Makes text bold",
			"Inserts a line break",
			"Starts a new paragraph",
			"Creates a border",
		],
		answer: 1,
	},
	{
		question: "Which tag is used to create a table row?",
		options: ["<tr>", "<td>", "<table>", "<row>"],
		answer: 0,
	},
	{
		question: "Which tag is used to add a comment in HTML?",
		options: [
			"// This is a comment",
			"# This is a comment",
			"<!-- This is a comment -->",
			"** This is a comment **",
		],
		answer: 2,
	},
];
const QuizPage = () => {
	const [current, setCurrent] = useState(0);
	const [score, setScore] = useState(0);
	const [selected, setSelected] = useState(null);
	const [finished, setFinished] = useState(false);
	const [userName, setUserName] = useState("");
	const [showCertificate, setShowCertificate] = useState(false);

	const handleNext = () => {
		if (selected === questions[current].answer) {
			setScore(score + 1);
		}
		if (current + 1 < questions.length) {
			setCurrent(current + 1);
			setSelected(null);
		} else {
			setFinished(true);
		}
	};

	const restartQuiz = () => {
		setCurrent(0);
		setScore(0);
		setSelected(null);
		setFinished(false);
		setShowCertificate(false);
	};

	const handleDownloadCertificate = () => {
		if (score >= 10) {
			const doc = new jsPDF({
				orientation: "landscape",
				unit: "mm",
				format: "a4",
			});

			const width = doc.internal.pageSize.getWidth();
			const height = doc.internal.pageSize.getHeight();

			const img = new Image();
			img.src = certificateBg;

			img.onload = () => {
				doc.addImage(img, "PNG", 0, 0, width, height);
				doc.setFont("helvetica", "bold");
				doc.setFontSize(28);
				doc.setTextColor(40, 40, 40);
				doc.text(userName, width / 2, 90, { align: "center" });

				doc.setFontSize(18);
				doc.setFont("helvetica", "normal");
				doc.text(
					`has successfully completed the HTML course`,
					width / 2,
					105,
					{ align: "center" }
				);

				doc.text(
					`with a score of ${score} out of ${questions.length}.`,
					width / 2,
					115,
					{ align: "center" }
				);

				const date = new Date().toLocaleDateString("en-GB");
				doc.setFontSize(14);
				doc.text(`Date: ${date}`, width / 2, 135, { align: "center" });

				doc.save(`${userName}_Certificate.pdf`);
			};
		}
	};

	const handleUserNameChange = (e) => {
		setUserName(e.target.value);
	};

	return (
		<div className="quiz-wrapper">
			<div className="quiz-card">
				<h2 className="quiz-title">HTML Quiz</h2>
				{finished ? (
					<div className="quiz-result">
						<h3>
							Your Score: {score} / {questions.length}
						</h3>
						{score >= 10 ? (
							<div>
								<p>Congratulations! You have passed the quiz.</p>
								{/* عرض نموذج الاسم إذا كانت الدرجة 10 أو أكثر */}
								{!showCertificate && (
									<div className="certificate-input">
										<input
											type="text"
											placeholder="Enter your name"
											value={userName}
											onChange={handleUserNameChange}
										/>
										<button
											onClick={() => setShowCertificate(true)}
											className="quiz-btn"
										>
											Generate Certificate
										</button>
									</div>
								)}
							</div>
						) : (
							<p>
								Sorry, you need at least 10 correct answers to get a
								certificate.
							</p>
						)}
					</div>
				) : (
					<div>
						<h4 className="quiz-question">
							{questions[current].question}
						</h4>
						<ul className="quiz-options">
							{questions[current].options.map((option, index) => (
								<li key={index}>
									<label>
										<input
											type="radio"
											name="option"
											checked={selected === index}
											onChange={() => setSelected(index)}
										/>{" "}
										{option}
									</label>
								</li>
							))}
						</ul>
						<button
							onClick={handleNext}
							className="quiz-btn"
							disabled={selected === null}
						>
							{current + 1 === questions.length ? "Finish Quiz" : "Next"}
						</button>
					</div>
				)}
				{/* عند اكتمال الشهادة */}
				{showCertificate && (
					<div className="certificate-card">
						<h2>Certificate of Completion</h2>
						<p>This is to certify that</p>
						<p className="user-name">{userName}</p>
						<p>
							has successfully completed the HTML Quiz with a score of{" "}
							{score} / {questions.length}.
						</p>
						<div className="certificate-actions">
							<button
								onClick={handleDownloadCertificate}
								className="quiz-btn"
							>
								Download Certificate
							</button>
							<button onClick={restartQuiz} className="quiz-btn">
								Try Again
							</button>
						</div>
					</div>
				)}
			</div>
		</div>
	);
};

export default QuizPage;
