// CourseBar.jsx

import "../../pages/Courses/CoursePage.css";

import React from "react";

const CourseBar = ({ title, time, index, checked, onCheck, onClick }) => {
	return (
		<div className="course-details-container" onClick={() => onClick(index)}>
			<input
				type="checkbox"
				checked={checked}
				onChange={(e) => {
					e.stopPropagation();
					onCheck(index);
				}}
			/>
			<div className="details">
				<h3>{title}</h3>
				<div className="time">
					<i className="fa-brands fa-youtube"></i>
					<p>{time}</p>
				</div>
			</div>
		</div>
	);
};

export default CourseBar;
