import "./CoursePage.css";
import React, { useState } from "react";
import CourseBar from "../../components/Util/CourseBar";
import Footer from "../../components/Util/Footer";
import CourseNavBar from "../../components/Util/CourseNavBar";
import vid1 from "../../images/html1.mp4";
import vid2 from "../../images/html2.mp4";
import vid3 from "../../images/html3.mp4";
import vid4 from "../../images/html4.mp4";
import vid5 from "../../images/html5.mp4";
import vid6 from "../../images/html6.mp4";
import vid7 from "../../images/html7.mp4";
import vid8 from "../../images/html8.mp4";
import vid9 from "../../images/html9.mp4";
import vid10 from "../../images/html10.mp4";

const videoList = [
	{ title: "1. Introduction and What is HTML?", time: "11min", src: vid1 },
	{ title: "2. Element Structure", time: "4min", src: vid2 },
	{ title: "3. First HTML Page", time: "9min", src: vid3 },
	{ title: "4. Head and Body", time: "8min", src: vid4 },
	{ title: "5. Comments and usecases", time: "4min", src: vid5 },
	{ title: "6. Doctype and Head", time: "3min", src: vid6 },
	{ title: "7. Heading and usecases", time: "6min", src: vid7 },
	{ title: "8. Syntax and Attributes", time: "4min", src: vid8 },
	{ title: "9. Paragraph element", time: "4min", src: vid9 },
	{ title: "10. Element Attributes", time: "4min", src: vid10 },
];

const CoursePage = () => {
	const [currentVideoIndex, setCurrentVideoIndex] = useState(0);
	const [completedVideos, setCompletedVideos] = useState(
		new Array(videoList.length).fill(false)
	);

	const handleVideoSelect = (index) => {
		setCurrentVideoIndex(index);
	};

	const handleCheck = (index) => {
		const updated = [...completedVideos];
		updated[index] = !updated[index];
		setCompletedVideos(updated);
	};

	const completedCount = completedVideos.filter(Boolean).length;
	const progress = Math.round((completedCount / videoList.length) * 100);

	return (
		<div>
			<CourseNavBar />

			<div className="all-videos">
				<div className="video-container" style={{ position: "relative" }}>
					{/* Circle Percentage */}
					<div className="progress-circle">{progress}%</div>

					<video
						key={videoList[currentVideoIndex].src}
						src={videoList[currentVideoIndex].src}
						controls
					/>

					{/* Course Info */}
					<div className="course-details">
						<hr />
						<div className="course-data">
							<h6 className="descreption">By the numbers</h6>
							<div className="numbers">
								<div>
									<div>Skill Level: Beginner Level</div>
									<div>Students: 0</div>
									<div>Languages: English</div>
									<div>Captions: Yes</div>
								</div>
								<div>
									<div>Lectures: 10</div>
									<div>Video: 1.5 hours</div>
								</div>
							</div>
						</div>

						<hr />
						<div className="course-data">
							<h6 className="descreption">Features</h6>
							<div className="middle-content">
								<div>
									Available on <span>iOS</span> and{" "}
									<span>Android</span>
								</div>
							</div>
						</div>

						<hr />
						<div className="course-data">
							<h6 className="descreption">Description</h6>
							<div className="end-content">
								<p>
									Welcome to the “Learn HTML for Beginners” course!
									This course is designed for those new to web
									development and seeks to demystify the building
									blocks of the web.
								</p>
								<p>
									Starting with the basics, you'll explore essential
									topics such as tags, elements, and attributes...
								</p>
								<p>
									You'll also discover best practices for writing
									semantic HTML and gain confidence building web pages.
								</p>
							</div>
						</div>
					</div>
				</div>

				<div className="videos">
					<h4 className="mb-4 fs-5 mx-3">Course Content</h4>
					{videoList.map((video, index) => (
						<CourseBar
							key={index}
							title={video.title}
							time={video.time}
							index={index}
							checked={completedVideos[index]}
							onCheck={handleCheck}
							onClick={handleVideoSelect}
						/>
					))}

					{progress >= 80 && (
						<button
							className="quiz-button"
							onClick={() => (window.location.href = "/Elearning/quiz")}
						>
							Go to Quiz
						</button>
					)}
				</div>
			</div>

			<Footer />
		</div>
	);
};

export default CoursePage;
