import React from "react";
import cat1 from "../../images/cat1.jpeg";
import { Rating } from "@mui/material";
const CardCat = ({ image,title,director,rate,price,oldPrice }) => {
	return (
		<div>
			<div className="course-card transition-transform duration-300 hover:scale-105">
				<img src={image} alt="" />
				<h3>{title}</h3>
				<p>{director}</p>
				<div className="rating-container">
					<div>{rate}</div>
					<Rating name="size-small" defaultValue={rate} precision={0.5} />
				</div>
				<div className="price">
					<span>{price}</span>
					<del>{oldPrice}</del>
				</div>
			</div>
		</div>
	);
};

export default CardCat;
