import React from "react";
import "./ComingSoon.css";

const ComingSoon = () => {
	return (
		<div className="coming-soon-container">
			<h1 className="coming-title">Coming Soon</h1>
			<p className="coming-subtitle">
				From automation of people processes to creating
				<br />
				an engaged and driven culture.
			</p>
			<div className="email-notify">
				<input
					type="email"
					placeholder="Please enter your email address"
					className="email-input"
				/>
				<button className="notify-button">Notify Me</button>
			</div>
			<p className="notify-footer">-_- Notify me when App is launched -_-</p>
		</div>
	);
};

export default ComingSoon;
