import React from "react";
import { Row, Col } from "react-bootstrap";
import "./ChoosePaymentMethod.css";
import { Link } from "react-router-dom";
const ChoosePayMethoud = () => {
	return (
		<div className="container">
			<div className="admin-content-text pt-5">Choose Payment</div>
			<div className="user-address-card my-3 px-3">
				<Row className="d-flex justify-content-between ">
					<Col xs="12" className="my-4">
						<input
							name="group"
							id="group1"
							type="radio"
							value="Pay with credit card"
							className="mt-2"
						/>
						<label className="mx-2" for="group1">
							Payment with credit Card
						</label>
					</Col>
				</Row>

				<Row className="mt-3">
					<Col xs="12" className="d-flex">
						<input
							name="group"
							id="group1"
							type="radio"
							value="Payment upon receipt"
							className="mt-2"
						/>
						<label className="mx-2" for="group1">
							Payment with vodafone cash
						</label>
					</Col>
				</Row>
			</div>

			<Row>
				<Col xs="12" className="d-flex justify-content-end">
					<div className="product-price d-inline   border">249.99$</div>

					<div className="product-cart-add px-3 pt-2 d-inline m-2">
						<Link
							to={"/course"}
							className="text-decoration-none text-light complete-purchase"
						>
							Complete your purchase{" "}
						</Link>
					</div>
				</Col>
			</Row>
		</div>
	);
};

export default ChoosePayMethoud;
